@import url('https://fonts.googleapis.com/css?family=Montserrat:400,700');

body {
    width: 100%;
    height: 100%;

    margin: 0;
    padding: 0;
    display: table;
    position: absolute;
    font-family: 'Montserrat', serif;

    background-image: url(https://source.unsplash.com/1920x1080/?+mountain);
    background-color: #111111;
    -webkit-background-size: cover;
    background-size: cover;
    background-attachment: fixed;
}

#root {
    width: 100%;
    height: 100%;

    margin: 0;
    padding: 0;
    display: table;
    position: absolute;
}
